.newAnimal {
  flex: 5;
  padding: 50px;
}

.newAnimalInput {
  height: 35px;
  border: none;
  border-bottom: 1px black solid;
  margin-top: 10px;
}

.newAnimalFormItem {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
  width: 30%;

  margin-right: 50px;
}

.newAnimalFormBtnGroup {
  display: flex;
}

.newAnimalBtn {
  padding: 10px 30px;
  border: none;
  font-weight: 700;
  color: white;
  margin-right: 30px;
}

.btnCadastrar {
  background-color: rgb(58, 158, 58);
}

.btnReset {
  background-color: rgb(177, 50, 50);
}
