.leftMenu {
  height: 100vh;
  background-color: rgb(201, 232, 250);
  flex: 1;
}
.leftMenuButtons {
  padding: 15px 10px;
  list-style: none;
}

.leftMenuItem {
  display: flex;
  align-items: center;
  height: 30px;
  font-size: 20px;
  font-weight: 300;
  cursor: pointer;
  color: rgb(3, 0, 0);
  margin-left: 10px;
  margin-bottom: 20px;
}

.leftMenuItem:hover {
  background-color: rgb(222, 196, 247);
}

.leftMenuIcon {
  font-size: 40px !important;
}
