.navBar {
  height: 75px;
  background-color: rgb(2, 34, 141);
  position: sticky;
  width: 100%;
}
.navBarWrapper {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0px 25px;
}
.logo {
  font-size: 25px;
  font-weight: bold;
  font-family: "Courier New", Courier, monospace;
  color: white;
}

.navBarIcon {
  font-size: 35px !important;
  margin-right: 10px;
  cursor: pointer;
  color: white;
}
