.animais {
  margin: 15px;
  width: 100%;
}
.topWrapper {
  display: flex;
  justify-content: space-between;
}
.tableWrapper {
  display: flex;
  justify-content: center;
  width: 100%;
}
.animaisTitle {
  font-size: 35px;
  font-weight: 700;
}
.animaisTableRow:hover {
  background-color: rgb(253, 211, 211);
}

.animaisTable {
  width: 75%;
  margin-top: 10px;
}

.tableColTitle {
  font-size: 20px;
  font-weight: 700;
}
.tableColContent {
  text-align: center;
}
.tableIconRemove {
  color: rgb(218, 8, 8);
  cursor: pointer;
}

.tableIconEdit {
  cursor: pointer;
}
